#root {
  width: 100%;
  height: 100%;
  display: flex;
}

.App {
  flex: 1;
  text-align: center;
  background: linear-gradient(45deg, #9ccef5 0%, #a8baf1 49%, #cab9ec 82%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App h1 {
  margin: 0;
  padding-bottom: 3rem;
}

.glassmorphism {
  background: rgba(255, 255, 255, 0.40);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(2.5px);
  -webkit-backdrop-filter: blur(2.5px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.conversation-container {
  height: 80vh;
  width: 60vh;
  overflow-x: hidden;
  overflow-y: auto;
}

.conversation-content {
  padding: 1.6rem;
  display: flex;
  flex-direction: column;
}

.message-container {
  margin: 0.3rem 0;
  max-width: 65%;
  border-radius: 1.2rem;
  box-shadow: 0 5px 10px 0 rgba(31, 38, 135, 0.37);
  padding: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-size: 1.2rem;
}

.message-container .text-message {
  text-align: left;
}

.message-container .image-message {
  border-radius: 0.8rem;
}

.message-container a {
  text-decoration: none;
}

.message-container .attachment-message {
  transition: all 0.16s ease-in;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 5rem;
  border-radius: 0.8rem;
  padding: 1rem
}

.message-container .attachment-message:hover {
  background-color: #ececec;
}

.message-container .attachment-message:active {
  background-color: #c4c4c4;
}

.message-container .attachment-message img {
  width: auto;
  height: 100%;
  opacity: 0.5;
  transform: scale(0.9);
  transition: all 0.16s ease-in;
}

.message-container .attachment-message:hover img {
  opacity: 1;
  transform: scale(1);
}

.message-container .attachment-message span {
  padding-left: 0.8rem;
  color: #5f5f5f;
  font-size: 1.4rem;
  font-weight: 500;
}

.message-container time {
  text-align: left;
  color: #888888;
  padding-top: 0.4rem;
  font-size: 1rem;
}

.message-container.agent {
  align-self: flex-end;
  background-color: #7989ac;
  border: 1px solid #7384aa;
  color: #fff;
}

.message-container.user {
  align-self: flex-start;
  background-color: #fff;
  border: 1px solid #b1b1b1;
}

.message-container.agent time {
  text-align: right;
  color: #b8caf1;
}